<template>
  <div>
    <div id="map" :class="$style['map']"></div>
    <Button @click="addPoint"></Button>
    <Button @click="removePoint"></Button>
    <Button @click="addLine"></Button>
    <Button @click="removeLine"></Button>
    <Button @click="addPolygon"></Button>
    <Button @click="removePolygon"></Button>
    <Button @click="addOverlay">Overlay</Button>
    <Button @click="removeOverlay">Overlay</Button>
  </div>
</template>
<script>
import Map from "./js/add-feature.js";

export default {
  name: "TileLayer",
  data() {
    return {
      map: null,
    };
  },
  mounted() {
    this.initMap();
  },
  methods: {
    initMap() {
      this.map = new Map();
    },
    addPoint() {
      let point = [120, 30];
      this.map.addPoint(point);
    },
    removePoint() {
      this.map.clearPoint();
    },
    addLine() {
      let line = [
        [120, 30],
        [121, 31],
      ];
      this.map.addLine(line);
    },
    removeLine() {
      this.map.clearLine();
    },
    addPolygon() {
      let polygon = [
        [
          [120, 30],
          [120, 31],
          [121, 31],
          [121, 30],
          [120, 30],
        ],
      ];
      this.map.addPolygon(polygon);
    },
    removePolygon() {
      this.map.clearPolygon();
    },
    addOverlay() {
      let coord = [120,30];
      this.map.addOverlay(coord)
    },
    removeOverlay() {
      this.map.clearOverlay()
    }
  },
};
</script>

<style lang="less" module>
.map {
  width: 100%;
  height: 500px;
}
</style>
